.game-profile {
  min-width: 0;
  margin-top: 50px;

  &__back {
    margin: 10px;
  }
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: start;
    width: 100%;
    margin: 20px auto;
  }
}

@media (max-width: 800px) {
  .game-profile {
    &__content {
      flex-flow: column-reverse;
      align-items: center;
      margin: 20px 5px;
    }
  }
}
